import noMore from '@/components/noMore.vue'
import loadErr from '@/components/loadErr.vue'

export default {
	data() {
		return {
			cutArr: [{
				title: '未使用',
				type: 1
			}, {
				title: '已使用',
				type: 2
			}, {
				title: '已过期',
				type: 3
			}],
			curType: 1,
			isLoadErr: false, //加载失败
			
			// 未使用
			nu_index: 1,
			nu_size: 10,
			nu_dataList: [],
			nu_isMore: true,
			
			// 已使用
			hu_index: 1,
			hu_size: 10,
			hu_dataList: [],
			hu_isMore: true,
			
			// 已过期
			ht_index: 1,
			ht_size: 10,
			ht_dataList: [],
			ht_isMore: true,
			
			couponArr: [{
				price: 10,
				limit: 99,
				name: '新人券',
				useTime: '2019-09-04',
				type: 0,//未使用
				id: 1
			}, {
				price: 10,
				limit: 99,
				name: '新人券',
				useTime: '2019-09-04',
				type: 1,//已使用
				id: 2
			}, {
				price: 10,
				limit: 99,
				name: '新人券',
				useTime: '2019-09-04',
				type: 2,//已过期
				id: 3
			}],
			curCouponId: null, //当前券id
		}
	},
	methods: {
		toCutCouponType(idx) {
			this.cutArr.map((v, i) => {
				if(idx == i) {
					this.curType = v.type
				}
			})
		},
		toShowCouponIntro(idx) {
			this.couponArr.map((v, i) => {
				if(idx == i) {
					if(this.curCouponId == v.id) {
						this.curCouponId = null
					}else {
						this.curCouponId = v.id
					}
				}
			})
		},
		
	}
}
